@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
html {
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    /* background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%); */
    width: 100%;
    height: 100%;
    overflow-y: hidden;
}

h1,h2,h3,p {
    font-family: 'Comic Neue', cursive;
    color: black;
}

h1 {
    margin: 5px;
    font-size: 3rem;
}

h2 {
    margin: 2vh;
    font-size: 1.8rem;
}

h3 {
    font-size: 1.35rem;
}

.center {
    text-align: center;
}

.hidden {
    visibility: hidden;
}

.bold {
    font-weight: 1000;
}
.Player {
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    height: 95vh;
    width: 100vw;
}

.playerContainer {
    margin: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 5fr 1fr 1fr;
}

.playerContainer #playerCode {
    text-align: center;
    grid-column-start: 2;
    align-self: center;
}

.playerContainer #songTitle {
    text-align: center;
    grid-column-start: 3;
    align-self: center;
}

.playerContainer #addedBy {
    text-align: center;
    grid-column-start: 4;
    align-self: center;
}

.playerContainer #artist {
    grid-column-start: 3;
    grid-row-start: 2;
    text-align: center;
    align-self: start;
}

.playerContainer #coverArt {
    grid-row-start: 3;
    grid-column-start: 3;
    align-self: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: auto;
}

.playerContainer #songLength {
    grid-row-start: 4;
    grid-column-start: 4;
    align-self: end;
    margin-left: 10px;
}

.playerContainer #timer {
    grid-row-start: 4;
    grid-column-start: 2;
    align-self: end;
    text-align: end;
    margin-right: 10px;
}


.playButton {
    position: absolute;
    top: 45vh;
    right: 47vw;
    z-index: 100;
}

.playButton button, input[type="submit"], input[type="reset"] {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.playerEvents {
    left: 1px;
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: 101;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.Player .spotifyAuthenticationHelper {
    display: none;
}
.MP3Player {
    visibility: hidden;
    position: absolute;
    top: -1;
    left: -1;
}
.Particles {
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
}

.Particles canvas {
    top: 0;
    left: 0;
}
.AdtRadEvent {
    display: flex;
    justify-content: center;
    align-items: center;
}

.AdtRadEvent .adtRad {
    z-index: 10;
}

.AdtRadEvent .tsparticles-canvas-el {
    z-index: 5;
}
.ShotsEvent .tsparticles-canvas-el {
    z-index: 5;
}

.ShotsEvent .shotsForeground {
    z-index: 10;
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.ShotsEvent img {
    width: 650px;
}
.EscalatieEvent {
    z-index: 10;
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.EscalatieEvent img {
    height: 700px;
    width: 700px;
}
.WaterEvent .tsparticles-canvas-el {
    z-index: 5;
}

.WaterEvent .waterForeground {
    z-index: 10;
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.WaterEvent img {
    width: 1200px;
}
.PlaylistCreator {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    height: 100vh;
}

.PlaylistCreator .Link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.PlaylistCreator #title {
    grid-column: 3;
}

.PlaylistCreator #new {
    grid-column: 2;
    grid-row: 2;
}

.PlaylistCreator #all {
    grid-column: 3;
    grid-row: 2;
}

.PlaylistCreator #my {
    grid-column: 4;
    grid-row: 2;
}
.CreateNewPlaylist {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    width: 100vw;
    height: 90vh;
    color: black;
}

.CreateNewPlaylist form {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 0.5em;
}

.CreateNewPlaylist form input[name="name"] {
    grid-row: 2;
}

.CreateNewPlaylist form #errorName {
    grid-row: 3;
}

.CreateNewPlaylist form input[name="username"] {
    grid-row: 4;
}

.CreateNewPlaylist form #errorUsername {
    grid-row: 5;
}

.CreateNewPlaylist form #duplicates {
    grid-row: 6;
}
.CreateNewPlaylist form button {
    grid-row: 7;
}

.CreateNewPlaylist form .error {
    color: darkred;
}

.CreateNewPlaylist form .errorContainer {
    height: 1em;
}

.CreateNewPlaylist h2 {
    text-align: center;
}
.AskForPlayerCode {
	display: grid;
	grid-template-columns: 1fr 10fr 1fr;
	grid-template-rows: 1fr 2fr 2fr 1fr;
}

.AskForPlayerCode h2 {
	grid-row: 2;
	grid-column: 2;
}

.AskForPlayerCode input {
	grid-row: 3;
	grid-column: 2;
}

.ControlPanel {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 8fr 1fr 1fr 1fr 2fr;
    height: 95vh;
    width: 100vw;
    position: absolute;
}

.ControlPanel #img {
    grid-row: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ControlPanel img {
    width: 75%;
}

.ControlPanel #buttonPanel {
    grid-row: 5;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.ControlPanel #pause {
    grid-column: 3;
}

.ControlPanel #skip {
    grid-column: 4;
}

.ControlPanel #title {
    grid-row: 3;
    text-align: center;

}

.ControlPanel #title h2 {
    margin: 0px;
}


.ControlPanel button, input[type="submit"], input[type="reset"] {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}
.EditPlaylist .buttonColumn {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 2fr;
    grid-row-gap: 3em;
}

.EditPlaylist .buttonContainer {
    display: flex;
    justify-content: center;
}

.EditPlaylist .button {
    min-width: 50vw;
}

.EditPlaylist .sharePlaylist {
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.EditPlaylist .sharePlaylist input {
    max-width: 90vw;
    margin: 10px;
}
.LoadingScreen {
    position: absolute;
    width: 100vw;
    height: 90vh;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.AddYoutube {
    margin-top: 10vh;
    display: grid;
    grid-template-rows: 1fr 16fr 8fr;
    grid-row-gap: 1em;
    height: 90vh;
}

.AddYoutube input {
    max-width: 90vw;
}

.AddYoutube form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.AddYoutube .noPreview {
    text-align: center;
}

.YoutubePreview {
    display: flex;
    width: 100vw;
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.YoutubePreview img {
    width: 90vw;
    height: 29vh;
}

.YoutubePreview p {
    width: 90vw;
    text-align: center;
    color: black;
}


.AddSpotify {
    overflow: hidden;
}

.ResultList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ResultList .pageControls {
    display: flex;
    justify-content: space-between;
    min-width: 10vw;
}

.Result {
    background-color: aliceblue;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 95vw;
    min-width: 95vw;
}

.Result .media-content {
    max-width: 34vw;
    min-width: 34vw;
    
    max-height: 22vh;
    min-height: 22vh;
}

.backButton {
    position: absolute;
    top: 10px;
    left: 10px;
}
.AddMP3 {
    width: 98vw;
    height: 90vh;
    display: grid;
    grid-template-rows: 1fr 1fr;
}

.AddMP3 .upload input {
    width: 75vw;
}

.AddMP3 .upload button {
    width: 20vw;
}
.PlaylistView {
    overflow-y: auto;
    height: 68vh;
    width: 90vw;
    display: grid;
    grid-row-gap: 5px;
}

.PlaylistView .playlistTitle {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

.PlaylistView .playlistTitle span {
    margin-bottom: 1em;
}
.AllLists .listGrid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.AllLists .listGrid .listSpacing {
    visibility: hidden;
}
.PlayPlaylist {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vw;
    width: 100vw;
}

.PlayPlaylist .wantEvents {
    margin: 10px;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.PlayPlaylist .wantEvents textarea {
    align-self: center;
}

.PlayPlaylist button {
    margin: 10px
}
.Home {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 2fr 1fr;
    height: 100vh;
    width: 100vw;
}

@media only screen and (max-width: 768px) {
    button {
        max-width: 32vw;
    }
} 

.Home #title {
    grid-column: 3;
    grid-row: 1;
    display: flex;
    align-items: center;
}

.Home #title h1 {
    text-align: center;
    width: 100%;
}

.Home .Link {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.Home #playlistButton {
    grid-row: 2;
    grid-column: 2;
}

.Home #controllerButton {
    grid-row: 2;
    grid-column: 3;
}

.Home .SpotifyAuthentication {
    grid-row: 2;
    grid-column: 4;
}

.home footer {
    grid-row: 3;
    grid-column-start: 1;
    grid-column-end: 5;
}
